import { Component, OnInit, Inject, ViewChildren, QueryList } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatInput } from '@angular/material';
import { Dialog } from 'src/app/models/dialog.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import * as moment from 'moment';

@Component({
	selector: 'postpane-dialog',
	templateUrl: './dialogPostpone.component.html',
	styleUrls: ['./dialogPostpone.component.scss']
})

export class DialogPostponeComponent implements OnInit {

	public form: FormGroup;
	public dataAtual: Date  = new Date();
	public amanha: Date;
	public maxDate: Date = moment().add(90, "days").toDate();

	@ViewChildren(MatInput) inputs: QueryList<MatInput>;
	
	constructor(
		public dialogRef: MatDialogRef<DialogPostponeComponent>,
		@Inject(MAT_DIALOG_DATA) public data: Dialog,
		private formBuilder: FormBuilder,
	) { }
	
	ngOnInit() {
		this.amanha = moment(this.dataAtual, "DD-MM-YYYY").add(1, 'days').toDate();
		this.form = this.formBuilder.group({
			dataPostergar: ['', [Validators.required]],
			justificativa: ['', [Validators.required]],
		});
	}

	public cancel() {
		this.dialogRef.close();
	}

	postergar() {
		if(this.form.valid) {
			this.dialogRef.close(this.form.value);
		}
	}
}
