import { Component, OnInit, Inject} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ResultAnalyzeAgencyFromTo } from 'src/app/models/result-analyze-agency-from-to.interface';


@Component({
	selector: 'from-to-dialog',
	templateUrl: './dialog-from-to.component.html',
	styleUrls: ['./dialog-from-to.component.scss']
})

export class DialogFromToComponent implements OnInit {

	public form: FormGroup;

	constructor(
		public dialogRef: MatDialogRef<DialogFromToComponent>,
		@Inject(MAT_DIALOG_DATA) public data: ResultAnalyzeAgencyFromTo,
		private formBuilder: FormBuilder,
	) { }
	
	ngOnInit() {
		this.form = this.formBuilder.group({
			uniorg: ['', [Validators.required]],
			numChamado: ['', [Validators.required]]
		});
	}

	public cancel() {
		this.dialogRef.close();
	}

	postergar() {
		if(this.form.valid) {
			this.dialogRef.close(this.form.value);
		}
	}
}
