import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OperationService } from 'src/app/services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { MatTableDataSource, MatSnackBar, PageEvent } from '@angular/material';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import * as moment from 'moment';
import { ServiceCenterService } from '../service/service-center.service';
import { ResponseSearchAnalyseToEdit } from 'src/app/models/response-search-analyse-to-edit.interface';

@Component({
	selector: 'app-search-analyse',
	templateUrl: './search-analyse.component.html',
	styleUrls: ['./search-analyse.component.scss']
})
export class SearchAnalyseComponent implements OnInit {
	
	public loading: boolean = false;
	public operations: Operacao[] = [];
	public activities: Atividade[] = [];
	public form: FormGroup;
	public statusList: string[][];
	public dataSource = new MatTableDataSource<ResponseSearchAnalyseToEdit>();
	public displayedColumns: string[] = ['esteiraId', 'dataAnalise', 'dataAlteracao', 'documento', 'agencia', 'conta', 'status', 'operador', 'actions'];
	public today: Date = new Date();
	public moduloAnaliseServiceCenter: boolean = false;

	/** Paginator */
	public length: number = null;
	public pageSize: number = 5;
	public pageIndex: number = 0;

	constructor (
		private formBuilder: FormBuilder,
		private operationService: OperationService,
		private matSnackBar: MatSnackBar,
		private serviceCenterService: ServiceCenterService
	) { }

	ngOnInit() {
		
		/** Lista de Operações COM Template Associado */
		this.loading = true;
		this.operationService.getOperations(true).subscribe(
			response => {
				this.operations = [...response.data];
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Operações e Atividades. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		);

		
		this.form = this.formBuilder.group({
			operacaoId: ['', [Validators.required]],
			atividadeId: ['', [Validators.required]],
			documento: [''],
			agencia: [''],
			conta: [''],
			dataInicial: [''],
			dataFinal: [''],
		});
	}

	public setOperation(): void {
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.form.value.operacaoId);
		this.activities = [...operation.atividades];
		this.form.get('atividadeId').enable();
		this.pageIndex = 0;
	}

	public setTipo(): void {
		const atividade  = this.activities.find(element=>element.id == this.form.value.atividadeId);
		this.moduloAnaliseServiceCenter = atividade.moduloAnaliseServiceCenter;
	}

	public submit(): void {
		if(this.moduloAnaliseServiceCenter == true) {
			this.displayedColumns = ['esteiraId', 'dataAnalise', 'dataAlteracao', 'im', 'status', 'operador', 'actions']
		} else {
			this.displayedColumns = ['esteiraId', 'dataAnalise', 'dataAlteracao', 'documento', 'agencia', 'conta', 'status', 'operador', 'actions'];
		}
		if (this.form.valid) {
			this.search();
		} else {
			this.form.markAllAsTouched();
		}
	}

	private search(): void {
		this.loading = true;
		this.length = null
		const form = this.form.value;
		const initialDate = form.dataInicial !== "" ? moment(new Date(form.dataInicial)).format('YYYY-MM-DD') : "";
		const finalDate = form.dataFinal !== "" ? moment(new Date(form.dataFinal)).format('YYYY-MM-DD') : "";

		this.serviceCenterService.searchAnalysis(
			form.atividadeId, form.agencia, form.conta, form.documento, initialDate, finalDate, this.pageSize, this.pageIndex + 1
		).subscribe(
			response => {
				this.length = response.length;
				this.dataSource = new MatTableDataSource<ResponseSearchAnalyseToEdit>(response.data);
				this.loading = false;
				
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter dados. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		)
	}

	public changePaginator(event: PageEvent): void {
		this.pageIndex = event.pageIndex;
		this.pageSize = event.pageSize;
		this.search();
	}

	public formatDocument(document: string): string {
		if (document && document.length === 14) {
			return document.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, "$1.$2.$3/$4-$5");
		} 
		
		if (document && document.length === 11) {
			return document.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, "$1.$2.$3-$4");
		} 
		return document;
	}
}