import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ServiceCenterRoutingModule } from './service-center-routing.module';
import { EditAnalyseComponent } from './edit-analyse/edit-analyse.component';
import { AgencyFromToComponent } from './agency-from-to/agency-from-to.component';
import { PostponeComponent } from './postpone/postpone.component';
import { SearchAnalyseComponent } from './search-analyse/search-analyse.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { MatCheckboxModule, MatDatepickerModule, MatNativeDateModule, MatDividerModule, MatFormFieldModule, MatInputModule, MatProgressSpinnerModule, MatRadioModule, MatSelectModule, MatPaginatorModule, MatTableModule, DateAdapter, MAT_DATE_FORMATS, MatCardModule, MatProgressBarModule } from '@angular/material';
import { NgxMaskModule } from 'ngx-mask';
import { ReactiveFormsModule } from '@angular/forms';
import { DialogPostponeComponent } from './postpone/dialogPostpone/dialogPostpone.component';
import { DialogFromToComponent } from './agency-from-to/dialog-from-to/dialog-from-to.component';
import { CustomDateAdapter } from 'src/app/shared/dateAdapter/customDate.adapter';
import { MY_DATE_FORMATS } from 'src/app/shared/dateAdapter/my-date-formats.const';
import { Upload3ngComponent } from './upload-3ng/upload-3ng.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { UploadAnaliseComponent } from './upload-analise/upload-analise.component';


@NgModule({
	declarations: [
		EditAnalyseComponent, 
		AgencyFromToComponent, 
		PostponeComponent, 
		SearchAnalyseComponent,
		DialogPostponeComponent,
		DialogFromToComponent,
		Upload3ngComponent,
		DashboardComponent,
		UploadAnaliseComponent
	],
	imports: [
		CommonModule,
		SharedModule,
		MatCheckboxModule,
		MatDatepickerModule,
		MatNativeDateModule,
		MatDividerModule,
		MatFormFieldModule,
		MatInputModule,
		MatProgressSpinnerModule,
		MatRadioModule,
		MatSelectModule,
		MatPaginatorModule,
		MatTableModule,
		NgxMaskModule.forRoot(),
		ReactiveFormsModule,
		ServiceCenterRoutingModule,
		MatProgressBarModule,
		MatCardModule
	],
	providers: [
		{ provide: DateAdapter, useClass: CustomDateAdapter },
		{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }
	],
	entryComponents : [
		DialogPostponeComponent,
		DialogFromToComponent
	]
})
export class ServiceCenterModule { }
