import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ServiceCenterService } from '../service/service-center.service';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar, MatTableDataSource, PageEvent } from '@angular/material';
import { DashboardAnalise } from 'src/app/models/dashboard-analise.interface';
import { ServiceCenterNG } from 'src/app/models/service-center-ng.interface';
import * as moment from 'moment';
import { trigger, state, transition, style, animate } from '@angular/animations';
import { DatePipe } from '@angular/common';

@Component({
	selector: 'app-service-center-dashboard',
	templateUrl: './dashboard.component.html',
	styleUrls: ['./dashboard.component.scss']
})

export class DashboardComponent implements OnInit {
	public file: Set<File> = new Set()
	public loading: boolean = false;
	public formSearch: FormGroup;
	public isUploadFiles: boolean = false;
	public displayedColumns: string[] = ['codigoExterno', 'subCategoria', 'statusServiceCenter', 'statusAnalise', 'operador', 'dataCadastro', 'dataAtendimento'];

	public dataSource = new MatTableDataSource<ServiceCenterNG>([]);
	public length: number;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public period: string = '1';
	public loader: boolean = false;
	public minDate: Date = moment().subtract(4, "months").toDate();
	public dataAtual = new Date();
	public pouchs: ServiceCenterNG[] = [];
	public dashboard: DashboardAnalise;


	constructor(
		private formBuilder: FormBuilder,
		private serviceCenterService: ServiceCenterService,
		private matSnackBar: MatSnackBar
	) { }

	ngOnInit() {
		this.formSearch = this.formBuilder.group({
			codigoExterno: [''],
			statusAnalise: [''],
			statusServiceCenter: [''],
			initialDate: ['', Validators.required],
			finalDate: ['', Validators.required],
		});

		//this.getDashboard();
	}

	public getDashboard(): void {
		if (this.formSearch.valid) {

			this.loading = true;
			this.serviceCenterService.dashboard().subscribe(
				response => {
					this.loading = false;
					this.dashboard = response.data;
				},
				() => {
					this.loading = false;
				}
			);
		}
	}

	public getList(): void {
		if (this.formSearch.valid) {
			this.loading = true;
			const form = this.formSearch.value;
			const initialDate = form.initialDate !== "" ? moment(new Date(form.initialDate)).format('YYYY-MM-DD') : "";
			const finalDate = form.finalDate !== "" ? moment(new Date(form.finalDate)).format('YYYY-MM-DD') : "";

			this.serviceCenterService.report(
				form.codigoExterno, 
				form.statusAnalise, 
				form.statusServiceCenter, 
				initialDate, 
				finalDate
			).subscribe(
				response => {
					this.loading = false;
					this.pouchs = response.data;
					this.length = response.length;
					this.dataSource = new MatTableDataSource<ServiceCenterNG>(this.pouchs);
				},
				() => {
					this.loading = false;
					this.resetForm();
				}
			);

		}
	}

	private resetForm(): void {
		this.formSearch.reset(
			{
				codigoExterno: '',
				statusAnalise: '',
				statusServiceCenter: '',
				initialDate: '',
				finalDate: '',
			}
		);
	}

	public convertDate(_date: string): string {
		if (_date && _date !== '') {
			const dateConvert = new Date(_date);
			return new Date(dateConvert.getTime() - (dateConvert.getTimezoneOffset() * 60000)).toJSON();
		}
		return '';

	}

	public download() {
		const form = this.formSearch.value;
		if ((form.initialDate != null && form.finalDate == null) || (form.finalDate != null && form.initialDate == null)) {
			SnackbarComponent.showAlert(this.matSnackBar, "Preencha a 'Data Inicial' e 'Data Final' corretamente!");
		} else {
			this.loading = true;
			this.serviceCenterService.reportDownload(form.codigoExterno, form.statusAnalise, form.statusServiceCenter, this.convertDate(form.initialDate), this.convertDate(form.finalDate)).subscribe(
				response => {
					const blob = new Blob([response.body], { type: 'application/octet-stream' });
					const linkArquivo = window.URL.createObjectURL(blob);
					const fileName = response.headers.get('content-disposition').split(';')[1].split('filename')[1].split('=')[1].trim();
					var link = document.createElement("a");
					link.setAttribute("href", linkArquivo.toString());
					link.setAttribute("download", fileName);
					link.style.display = "none";
					document.body.appendChild(link);
					link.click();
					document.body.removeChild(link);
					this.loading = false;
					this.resetForm();
				},
				() => {
					this.loading = false;
					SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter dados. Se o erro persistir, entre em contato com o suporte.");
					this.resetForm();
				}
			);
		}
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getList();
	}
}