import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ResponseList } from '../../../models/response-list.interface';
import { map } from 'rxjs/operators';
import { PostAnalyzeAgencyFromTo } from 'src/app/models/post-analyze-agency-from-to.interface';
import { PutPostpone } from 'src/app/models/put-postpone.class';
import { ResponseObject } from 'src/app/models/response-object.interface';


@Injectable({
	providedIn: 'root'
})
export class ServiceCenterService {
	public urlServiceCenter = "/api/v1/service-center";

	constructor(
		private http: HttpClient,
	) { }

	public putPostergar(body: PutPostpone): Observable<ResponseList> {
		return this.http.put<ResponseList>(environment.urlServices + this.urlServiceCenter + '/postergacoes/', body);
	}

	public getItensPostergacao(atividadeId: string, documento: string, agencia: string, contaCorrente: string, dataInicial: string, dataFinal: string, PageSize: string, PageIndex: string) {
		return this.http.get<ResponseList>(environment.urlServices + this.urlServiceCenter + '/atividade/' + atividadeId + '/postergacoes', {
			params: {
				atividadeId: atividadeId,
				documento: documento,
				agencia: agencia,
				contaCorrente: contaCorrente,
				dataInicial: dataInicial,
				dataFinal: dataFinal,
				PageSize: PageSize,
				PageIndex: PageIndex
			}
		});
	}

	public searchAnalysis(
		atividadeId: string, agencia: string, contaCorrente: string, documento: string, dataInicial: string, dataFinal: string, pageSize: number, pageIndex: number
	): Observable<ResponseList> {
		return this.http.get<any>(`${environment.urlServices}${this.urlServiceCenter}/atividade/${atividadeId}/edicoes`, {
			params: {
				Agencia: agencia,
				ContaCorrente: contaCorrente,
				Documento: documento,
				DataIncial: dataInicial,
				DataFinal: dataFinal,
				PageSize: pageSize.toString(),
				PageIndex: pageIndex.toString(),
			}
		});
	}

	public postDePara(body: PostAnalyzeAgencyFromTo): Observable<ResponseList> {
		return this.http.post<ResponseList>(environment.urlServices + this.urlServiceCenter + '/de-para', body);
	}

	public getItensDePara(atividadeId: string, documento: string, agencia: string, contaCorrente: string, dataInicial: string, dataFinal: string, PageSize: string, PageIndex: string) {
		return this.http.get<ResponseList>(environment.urlServices + this.urlServiceCenter + '/atividade/' + atividadeId + '/de-para', {
			params: {
				documento: documento,
				agencia: agencia,
				contaCorrente: contaCorrente,
				dataInicial: dataInicial,
				dataFinal: dataFinal,
				PageSize: PageSize,
				PageIndex: PageIndex
			}
		});
	}

	public getAnalyze(esteiraId: string): Observable<any> {
		return this.http.get<any>(`${environment.urlServices}${this.urlServiceCenter}/edicoes/${esteiraId}`).pipe(
			map(response => response.data)
		)
	}

	public putAnalyseEdited(esteiraId: string, body: any): Observable<any> {
		return this.http.put<any>(`${environment.urlServices}${this.urlServiceCenter}/edicoes/${esteiraId}`, body)
	}

	public upload3ng(file: any): Observable<any> {
        return this.http.post<Observable<any>>(`${environment.urlServices}${this.urlServiceCenter}/upload-ng`, file)
	}

	public uploadAnalise(file: any): Observable<any> {
        return this.http.post<Observable<any>>(`${environment.urlServices}${this.urlServiceCenter}/upload/analise`, file)
	}

	public report(codigoExterno: string, statusAnaliseId: string, statusServiceCenterId: string, initialDate: string, finalDate: string): Observable<any> {
		return this.http.get<Observable<any>>(`${environment.urlServices}${this.urlServiceCenter}/relatorio-ng`, {
			params: {
				CodigoExterno: codigoExterno,
				StatusProcedente: statusAnaliseId,
				StatusId: statusServiceCenterId,
				DataInicial: initialDate,
				DataFinal: finalDate
			}
		})
	}

	public reportDownload(codigoExterno: string, statusAnaliseId: string, statusServiceCenterId: string, initialDate: string, finalDate: string): Observable<HttpResponse<any>> {
		return this.http.get<any>(`${environment.urlServices}${this.urlServiceCenter}/relatorio-ng`, {
			responseType : 'blob' as 'json',
			params: {
				CodigoExterno: codigoExterno,
				StatusProcedente: statusAnaliseId,
				StatusId: statusServiceCenterId,
				DataInicial: initialDate,
				DataFinal: finalDate,
				ExportaXLSX: 'true'
			},
			observe : 'response'
		})
	}

	public dashboard() {
		return this.http.get<ResponseObject>(environment.urlServices + this.urlServiceCenter + '/dashboard-ng');
	}

	public atividade(atividadeId: string): Observable<any> {
		return this.http.get<Observable<any>>(`${environment.urlServices}/api/v1/atividades/${atividadeId}`);
	}
}