import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OperationService } from 'src/app/services/operation.service';
import { Operacao } from 'src/app/models/operacao.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { MatTableDataSource, MatSnackBar, PageEvent, MatDialog } from '@angular/material';
import { ServiceCenterService } from '../service/service-center.service';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { DialogFromToComponent } from './dialog-from-to/dialog-from-to.component';
import { DatePipe } from '@angular/common';
import { ResultAnalyzeAgencyFromTo } from 'src/app/models/result-analyze-agency-from-to.interface';
import { PostAnalyzeAgencyFromTo } from 'src/app/models/post-analyze-agency-from-to.interface';

@Component({
	selector: 'app-agency-from-to',
	templateUrl: './agency-from-to.component.html',
	styleUrls: ['./agency-from-to.component.scss']
})

export class AgencyFromToComponent implements OnInit {
	public loading: boolean = false;
	public operations: Operacao[] = [];
	public activities: Atividade[] = [];
	public form: FormGroup;
	public statusList: string[][];
	public dataSource = new MatTableDataSource<ResultAnalyzeAgencyFromTo>();
	public displayedColumns: string[] = ['dataCarga', 'documento', 'agencia', 'conta', 'historico', 'uniorg', 'actions'];
	public dataAtual: Date = new Date();
	public length: number = null;
	public pageSize: number = 5;
	public pageIndex: number = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	
	constructor(
		private formBuilder: FormBuilder,
		private operationService: OperationService,
		private serviceCenterService: ServiceCenterService,
		private matSnackBar: MatSnackBar,
		public dialog: MatDialog,
	) { }
		
	ngOnInit() {
		/** Lista de Operações COM Template Associado */
		this.loading = true;
		this.operationService.getOperations(true).subscribe(
			response => {
				this.operations = [...response.data];
				this.loading = false;
			},
			() => {
				SnackbarComponent.showError(this.matSnackBar, null, "Erro ao obter a lista de Operações e Atividades. Se o erro persistir, entre em contato com o suporte.");
				this.loading = false;
			}
		);

		this.form = this.formBuilder.group({
			operacaoId: ['', Validators.required],
			atividadeId: ['', Validators.required],
			dataInicial: [''],
			dataFinal: [''],
			documento: [''],
			agencia: [''],
			conta: ['']
		});
	}
			
	public setOperation(): void {
		const operation: Operacao = this.operations.find(el => el.operacaoId === this.form.value.operacaoId);
		this.activities = [...operation.atividades];
		this.form.get('atividadeId').enable();
		this.pageIndex = 0;
	}
				
	public getAtividadesDePara() {
		if (this.form.valid) {
			const form = this.form.value;
			var datePipe = new DatePipe("en-US");
			const dataInicial = (form.dataInicial != ''? datePipe.transform(form.dataInicial, 'yyyy-MM-dd') : '');
			const dataFinal = (form.dataFinal != ''? datePipe.transform(form.dataFinal, 'yyyy-MM-dd') : '');
			const pageIndex = (this.pageIndex  + 1).toString();
			const pageSize = this.pageSize.toString();
			this.loading = true;
			this.serviceCenterService.getItensDePara(form.atividadeId, form.documento, form.agencia, form.conta , dataInicial, dataFinal, pageSize, pageIndex ).subscribe(
				response => {
					this.loading = false;
					this.length = response.length;
					this.dataSource = new MatTableDataSource<ResultAnalyzeAgencyFromTo>(response.data);
				},
				() => {
					this.loading = false;
				}
			);
		}
	}
					
	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.getAtividadesDePara();
	}

	public formatDocument(document: string): string {
		if (document.length === 14) {
			return document.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, "$1.$2.$3/$4-$5");
		} else if (document.length === 11) {
			return document.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, "$1.$2.$3-$4");
		}
	}
					
	public dePara(element : ResultAnalyzeAgencyFromTo): void {
		const dialogRef = this.dialog.open(DialogFromToComponent, {
			width: '600px',
			data: element
		});
		
		dialogRef.afterClosed().subscribe(result => {
			if(result !== undefined ) {
				const body: PostAnalyzeAgencyFromTo = new PostAnalyzeAgencyFromTo();
				body.cargaId = element.cargaId;
				body.numeroIM = result.numChamado;
				body.uniorgNovo = result.uniorg;

				this.serviceCenterService.postDePara(body).subscribe(
					() => {
						SnackbarComponent.showSuccess(this.matSnackBar,'Uniorg Atualizado com Sucesso!');
						this.getAtividadesDePara();
					},
					() => {
						SnackbarComponent.showError(this.matSnackBar,'Erro ao cadastrar o Uniorg');
					}
				);
			}
		});
	}
}