import { Component } from '@angular/core';
import { ServiceCenterService } from '../service/service-center.service';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar } from '@angular/material';
import { MediaUpload } from 'src/app/models/media-upload.interface';

@Component({
	selector: 'app-upload-3ng',
	templateUrl: './upload-analise.component.html',
	styleUrls: ['./upload-analise.component.scss']
})

export class UploadAnaliseComponent  {

  	public file: Set<File> = new Set()
	public loading: boolean = false;
	public isUploadFiles: boolean = false;
	public anexos: MediaUpload[] = [];

	constructor(
		private serviceCenterService: ServiceCenterService,
		private matSnackBar: MatSnackBar
	) { }
		
	public onFileChanged(files: FileList) {
		if (files && files.length > 0) {
			this.anexos = [];
			try
			{
				this.isUploadFiles = true;
				for(let i = 0; i < files.length; i++){
					const fileToUpload: File = files.item(i);
					const formData: FormData = new FormData();
					formData.append('file', fileToUpload, fileToUpload.name);
					this.serviceCenterService.uploadAnalise(formData).subscribe(
						() => { 
							this.isUploadFiles = false;
							SnackbarComponent.showSuccess(this.matSnackBar, "Upload realizado com sucesso!");
							(<HTMLInputElement>document.getElementById('file')).value = "";
						},
						() => {
							this.isUploadFiles = false;
							SnackbarComponent.showError(this.matSnackBar, null , "Erro no upload do arquivo");
							(<HTMLInputElement>document.getElementById('file')).value = "";
						}
					);
				}
			}
			catch
			{
				this.isUploadFiles = false;
			}
		}
	}	
}