import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EditAnalyseComponent } from './edit-analyse/edit-analyse.component';
import { PostponeComponent } from './postpone/postpone.component';
import { AgencyFromToComponent } from './agency-from-to/agency-from-to.component';
import { SearchAnalyseComponent } from './search-analyse/search-analyse.component';
import { Upload3ngComponent } from './upload-3ng/upload-3ng.component';
import { DashboardComponent } from '../service-center/dashboard/dashboard.component';
import { UploadAnaliseComponent } from './upload-analise/upload-analise.component';

const routes: Routes = [
	{ path: 'editar-analise', component: SearchAnalyseComponent },
	{ path: 'editar-analise/:esteiraId/:atividadeId', component: EditAnalyseComponent },
	{ path: 'postergar', component: PostponeComponent },
	{ path: 'agencia-de-para', component: AgencyFromToComponent },
	{ path: 'upload-3ng', component: Upload3ngComponent },
	{ path: 'dashboard', component: DashboardComponent },
	{ path: 'upload-analise', component: UploadAnaliseComponent },
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class ServiceCenterRoutingModule { }
